.NAGWPage > .flex-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-left: 10%;
    padding-right: 10%;
    cursor: crosshair;
    height: 100%;

}

.col-1 {
    width: 8.33vw;
}
.col-2 {
    width: 16.66%;
}
.col-3 {
    width: 25%;
}
.col-4 {
    width: 33.33%;
}
.col-5 {
    width: 41.66%;
}
.col-6 {
    width: 50%;
}
.col-7 {
    width: 58.33%;
}
.col-8 {
    width: 66.66%;
}
.col-9 {
    width: 75%;
}
.col-10 {
    width: 83.33%;
}
.col-11 {
    width: 91.66%;
}
.col-12 {
    width: 100%;
}

/* Text */

h2 {
    font-family: 'Frank Ruhl Libre';
    color: #c997ff;
    font-size: 18px;
    letter-spacing: 1.5em;
    text-align: center;
    position: relative;
    left: 5%;
}

.nagwText {
    font-family: 'Frank Ruhl Libre';
    color: #FFB500;
    word-wrap: normal;
    text-align: left;
}

.nagwTextCenter {
    text-align: center;
}

.purple {
    color: #c997ff;
}

/* Link */
#link2016:target:before {
    content: "";
    display: block;
    height: 200px;
    margin: -200px 0 0;
}

/* Images */

.ImageFiveNAGW,
.ImageFourNAGW,
.ImageOneNAGW,
.ImageSixNAGW,
.ImageThreeNAGW,
.ImageTwoNAGW {
    max-width: 100%;
    max-height: auto;
    flex-wrap: wrap;
}

/* Hover Overlay */

.container {
    position: relative;
    width: auto;
    max-width: 100%;
}

.NAGWoverlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0;
    transition: 0.1s ease;
    background-color: #c997ff;

}
.container:hover .NAGWoverlay {
    opacity: 0.2;
}

/* Border */

.ImageBorderNAGWEnd,
.ImageBorderNAGWOne,
.ImageBorderNAGWThree,
.ImageBorderNAGWTwo {
    max-width: 100%;
    max-height: 25%;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

/* Media Queries */

/*smartphone portrait*/
@media screen and (min-width: 0px) and (max-width: 479px) {

    .columnOneNAGW,
    .columnTwoNAGW {
        width: 100%;
        height: auto;
        flex-direction: column;
        padding-bottom: 1%;
        height: 80%;
        flex-wrap: wrap;
    }

    .columnOneNAGW {
        order: 2;
        -webkit-order: 2;

    }
    .columnTwoNAGW {
        order: 1;
        -webkit-order: 1;
    }

    .nagwText {
        height: 100%;
        font-size: 13px;
        padding-bottom: 5%;
    }

    .ImageBorderNAGWEnd,
    .ImageBorderNAGWTop {
        display: none;
    }

    .ImageBorderNAGWOne {
        padding-top: 10%;
    }
    #link2016:target:before  {
        content:"";
        display:block;
        height:120px; 
        margin:-120px 0 0;
          }
}

/*smartphone landscape*/
@media screen and (min-width: 480px) and (max-width: 767px) {

    .columnFive,
    .columnFour,
    .columnOneNAGW,
    .columnThree,
    .columnTwoNAGW {
        width: auto;
        flex-direction: column;
        padding-bottom: 1%;
        flex-wrap: wrap;
    }

    .ImageBorderNAGWEnd,
    .ImageBorderNAGWTop {
        display: none;
    }

    .ImageBorderNAGWOne {
        padding-top: 5%;
    }

    .nagwText {
        padding-bottom: 5%;
        font-size: 12px;
    }

    .ImageBorderNAGWTwo {
        padding-bottom: 5%;
    }

    .columnOneNAGW {
        order: 2;
        -webkit-order: 2;

    }
    .columnTwoNAGW {
        order: 1;
        -webkit-order: 1;
    }
    #link2016:target:before {
        content: "";
        display: block;
        height: 150px;
        margin: -150px 0 0;
    }

}

/*tablet portrait*/
@media screen and (min-width: 768px) and (max-width: 1023px) {

    .ImageBorderNAGWEnd,
    .ImageBorderNAGWThree,
    .ImageBorderNAGWTop {
        width: 100%;
    }

    .ImageBorderNAGWEnd {
        padding-top: 5%;
    }

    .ImageBorderNAGWTwo {
        padding-bottom: 5%;
    }

    .nagwText {
        font-size: 13px;
        overflow: scroll;
        overflow-x: hidden;
        scrollbar-width: thick;
        padding-right: 2%;
        height: 30em;

    }

    h2 {
        font-size: 40px;
    }

    #link2016:target:before {
        content: "";
        display: block;
        height: 250px;
        margin: -250px 0 0;
    }

  

}

